import { Component, ViewEncapsulation, ViewChild } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { BsModalRef } from 'ngx-bootstrap';
import { TemplateViewModel } from '../../modal.model';
import { LoaderComponent } from '../../../loader';

@Component({
    selector: 'katana-template',
    templateUrl: './template.component.html',
    styleUrls: ['./template.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class TemplateComponent {
    @ViewChild('loader', { static: true }) public loader: LoaderComponent;
    public data: TemplateViewModel;
    constructor(
        private _sanitizationService: DomSanitizer,
        public _modalRef: BsModalRef) {
    }

    public bypassSecurityTrustHtml(html: string): SafeHtml {
        return this._sanitizationService.bypassSecurityTrustHtml(html);
    }

    public cancel() {
        if (this.data.cancelCallback) {
            this.data.cancelCallback(() => {
                this._modalRef.hide();
            });
        } else {
            this._modalRef.hide();
        }
        if (this.data.autoClose != false) {
            this._modalRef.hide();
        }
    }

    public accept() {
        if (this.isValid()) {
            if (this.data.acceptCallback) {
                this.loader.componentRef.instance.callback(this.data.mode).subscribe((response) => {
                    this.data.acceptCallback(response, this.loader.componentRef.instance);
                    this._modalRef.hide();
                });

            } else {
                this._modalRef.hide();
            }
            if (this.data.autoClose != false) {
                this._modalRef.hide();
            }
        }
    }

    public show(): boolean {
        if (this.loader && this.loader.componentRef && this.loader.componentRef.instance) {
            return this.loader.componentRef.instance.show();
        } else {
            return false;
        }
    }

    public isValid(): boolean {
        if (this.loader && this.loader.componentRef && this.loader.componentRef.instance && this.loader.componentRef.instance.isValid) {
            return this.loader.componentRef.instance.isValid();
        } else {
            return false;
        }
    }
}
